import React from 'react'
import './team.css'
import team from '../utils/config/schema/team'

function Team() {
    return (
        <div className="teams">
            <h1>Faculty Leads</h1>
            <hr/>
            <div className="main-man">
            {team.map((ele,index)=>{
                if(ele.designation === "NSS Coordinator"){
                    return(
                        <div className="" key={index}>
                        <div className="card">
         <div className="imgBx">
            <img src={ele.photo} alt="images"/>
         </div>
         <div className="details">
            <h2>{ele.name}<br/><span>{ele.designation}</span></h2>
          </div>
       </div>
                        </div>
                )
                }
                else{
                    return null;
                }
            })}
            </div>
            <br/>
            <div className="grids">
            {team.map((ele,index)=>{
                if(ele.type === "officer"){
                    return(
                        <div className={ele.designation} key={index}>
                        <div className="card">
         <div className="imgBx">
            <img src={ele.photo} alt="images"/>
         </div>
         <div className="details">
            <h2>{ele.name}<br/><span>{ele.designation}</span></h2>
          </div>
       </div>
                        </div>
                )
                }
                else{
                    return null;
                }
            })}
            </div>
            <h1>Student Leads</h1>
            <hr/>

            <div className="grids">
            {team.map((ele,index)=>{
                if(ele.type === "core"){
                    return(
                        <div className="" key={index}>
                        <div className="card">
         <div className="imgBx">
            <img src={ele.photo} alt="images"/>
         </div>
         <div className="details">
            <h2>{ele.name}<br/><span>{ele.designation}</span></h2>
          </div>
       </div>
                        </div>
                )
                }
                else{
                    return null;
                }
            })}
            </div>

        </div>
    )
}

export default Team