import React,{useState} from 'react';
import {
    HashRouter as Router,
    Switch,
    Route,
    useRouteMatch,
  } from "react-router-dom";
import Events from './main-event/event';
import Talk from './talkEvents/talk';
import talkSchema from '../utils/config/schema/talk'

function EventRoutes(){
    const {path,url} = useRouteMatch()
      return(
          <div style={{paddingTop:91}}>
          <Switch>
          <Route exact path={`${path}`}>
          <Events />
          </Route>
          </Switch>
          {talkSchema.map((ele,index)=>{
            return(
          <Route path={`${path}/${ele.name}`} key={index}>
          <Talk render = {ele}/>
          </Route>
            )
          })}
          </div>
      )
  }
  export default EventRoutes;



