import React,{useEffect,useState} from 'react'
import './event.css'
import TeamDialog from './eventDialog';
import months,{getMonths,getDates,getYear,currentTime} from '../../utils/utility'
import eventSchema from '../../utils/config/schema/event'

function Events() {
    var date = new Date().getMonth();
    const[isUpcoming,setUpcoming] = useState(false)
    const [open,setOpen] = useState(false);
    const [active,setActive] = useState("")
    const [today,setToday] = useState()
    useEffect(()=>{
        let date = new Date();
        let temp = 0;
        eventSchema.map((ele,index)=>{
            if(currentTime(ele.date)>=date.getTime()){
                temp++;
                console.log(temp)
            }
            if(temp>0){
                setUpcoming(true)
            }
            else{
                setUpcoming(false)
            }
        })
    })

    useEffect(()=>{
        setToday(new Date());
    },[new Date().getDate()])

    const handleClickOpen = (name) => {
        setOpen(true);
        setActive(name);
      };
    
      const handleClose = () => {
          setOpen(false)
      };


return (
    <div className="event">
        <div className="sidebar">
        <aside>
  <p> Events </p>
  {isUpcoming&&   <a href="#upcoming__events">
    <i className="fa fa-user-o" aria-hidden="true"></i>
    Upcoming Events
  </a>}
  {eventSchema.map((ele,index)=>{
      return(
        <a href="#" key={index}>
    <i className="fa fa-calendar" aria-hidden="true"></i>
    {ele.name}
  </a>
      )
  })}
  <a href="/#/events/socio-talks">
  <i className="fa fa-microphone" aria-hidden="true"></i>
    Socio-Talks
  </a>
</aside>
        </div>
        <main className="events__main">
            <div className="upcoming__events" id="upcoming__events">
            <ul>
            {eventSchema.map((ele,index)=>{
                let currentDate = new Date(today).getTime()
if(currentTime(ele.date) >= currentDate){
    return(
<div className="upcoming__events__card" key={index}>
<li className="event__card" aria-labelledby="event card">
        <TeamDialog open={(active===ele._id)?open:false} handleClose={handleClose} member={ele} getYear={getYear} getMonths={getMonths} getDates={getDates} months={months}/>
<div className="card__filter">
<img className="card__photo" src={ele.photo} alt={ele.name}/>
</div>
<div className="card__container">
<h4>{ele.name}</h4>
<time>{`${months[getMonths(ele.date)]} - ${getDates(ele.date)}, ${getYear(ele.date)}`}</time>
<p>{ele.description.slice(0,75)} ....</p>
<div className="card__buttons">
<a className="card__buttons btn primary" role="button" aria-haspopup="false" onClick={()=>{handleClickOpen(ele._id)}}>
Details<i className="fas fa-arrow-right"></i>
<div className="card__fill"></div>
</a>
</div>
</div>
</li>
</div>
    )
}
            })}
            </ul>
            </div>

            <div className="ended__events" id="ended__events">
            <ul>
            {eventSchema.map((ele,index)=>{
                let currentDate = new Date(today).getTime()
if(currentTime(ele.date) <= currentDate){
    return(
        <li className="event__card" aria-labelledby="event card" key={index}>
        <TeamDialog open={(active===ele._id)?open:false} handleClose={handleClose} member={ele} getYear={getYear} getMonths={getMonths} getDates={getDates} months={months}/>
<div className="card__filter">
<img className="card__photo" src={ele.photo} alt="NSS Event"/>
</div>
<div className="card__container">
<h4>{ele.name}</h4>
<time>{`${months[getMonths(ele.date)]} - ${getDates(ele.date)}, ${getYear(ele.date)}`}</time>
<p>{ele.description.slice(0,75)} ....</p>
<div className="card__buttons">
<a className="card__buttons btn primary" role="button" aria-haspopup="false" onClick={()=>{handleClickOpen(ele._id)}}>
Details<i className="fas fa-arrow-right"></i>
<div className="card__fill"></div>
</a>
</div>
</div>
</li>
    )
}
            })}
            </ul>
            </div>
        </main>
    </div>
)
}

export default Events


