function getMonths(newdate){
    var month = new Date(newdate);
    return month.getMonth();
}

function getDates(newdate){
    var date = new Date(newdate);
    return date.getDate()
}

function getYear(newdate){
    var date = new Date(newdate);
    return (date.getFullYear())
}

function getRandom(){
    var index = Math.floor(Math.random()*4)
    return index;
}
function currentTime(newdate){
    var date = new Date(newdate);
    return (date.getTime())
}

const months=['JAN','FEB','MAR','APR','MAY','JUN','JUL','AUG','SEP','OCT','NOV','DEC']
export default months
export {getMonths,getDates,getYear,getRandom,currentTime};