import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import './eventDialog.css'

export default function TeamDialog({open,handleClose,member,getDates,getMonths,getYear,months}) {

  return (
    <div style={{zIndex:10000}}>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
            <div className="event-dialog-title">
            <img src={member?.photo} alt=""/>
            <br/>
            <br/>
            <h2>{member?.name}</h2>
            <time>{`${months[getMonths(member.date)]} - ${getDates(member.date)}, ${getYear(member.date)}`}</time>
            <hr/>
            </div>
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {member?.description}
          </DialogContentText>
        </DialogContent>
      </Dialog>
    </div>
  );
}

